import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  Sec,
  SeeMore,
  SeeMore2,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  BackgroundCont,
  SocialHr,
  GoBack,
} from '../styles/searchstyles';
import {
  DetailWrapper,
  LeftSide,
  RightSide,
  ITitle,
  BasInfo,
  Overview,
  CompanyImage,
  CompanyWrapper,
  CompanyTitle,
  Credits,
} from './detailsmovie.styles';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import CloseIcon from '@material-ui/icons/Close';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grow from '@material-ui/core/Grow';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import ImdbIcon from '../../img/imdb_icon.png';
import TvdbIcon from '../../img/tvdb_icon.png';
import FanartIcon from '../../img/fanart_icon.ico';
import HomeIcon from '@material-ui/icons/Home';
import { ReactComponent as TmdbIcon } from '../../img/tmdb_icon.svg';
import { ReactComponent as TraktIcon } from '../../img/trakt_icon.svg';
import FastAverageColor from 'fast-average-color';
import { useLocation } from 'react-router-dom';
import StarIcon from '@material-ui/icons/Star';

function Detailsmovie(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [item, setItem] = useState('');
  const [videos, setVideos] = useState('');
  const [actorsCount, setActorsCount] = useState(14);
  const [vid, setVid] = useState(2);
  const [art1, setArt1] = useState(6);
  const [art2, setArt2] = useState(6);
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const [showImages, setShowImages] = useState(false);
  const [movie_idd, setMovieId] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    }
  };
  const handleMoreVideos = () => {
    if (vid === 1000) setVid(2);
    else setVid(1000);
  };
  const handleMoreImg = (a) => {
    switch (a) {
      case 1:
        if (art1 === 1000) setArt1(6);
        else setArt1(1000);
        break;
      case 2:
        if (art2 === 1000) setArt2(6);
        else setArt2(1000);
        break;
      default:
        break;
    }
  };
  const handleMore = () => {
    if (actorsCount === 1000) setActorsCount(18);
    else setActorsCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  // const getAverageColor = (href) => {
  //   const fac = new FastAverageColor();
  //   let imageURL = href;

  //   let downloadedImg = new Image();
  //   downloadedImg.crossOrigin = 'Anonymous';
  //   downloadedImg.src = imageURL;
  //   fac
  //     .getColorAsync(downloadedImg)
  //     .then((color) => {
  //       setAverageColor(color.rgb);
  //     })
  //     .catch((e) => {
  //       console.log(e);
  //     });
  // };
  const getStatus = (status) => {
    if (status === 'Canceled') return 'Anulowany';
    else if (status === 'Ended') return 'Zakoczony';
    else if (status === 'In Production') return 'W produkcji';
    else if (status === 'Pilot') return 'Pilot';
    else if (status === 'Planned') return 'Planowany';
    else if (status === 'Post Production') return 'Postprodukcja';
    else if (status === 'Released') return 'Wydany';
    else if (status === 'Rumored') return 'Pogoski';
    else if (status === 'Returning Series') return 'Powracajcy sezon';
    else if (status === 'Released') return 'Powracajcy sezon';
    else return status;
  };
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });
  const getGenres = () => ({
    28: { name: 'Akcja' },
    12: { name: 'Przygodowy' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Krymina' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    14: { name: 'Fantasy' },
    36: { name: 'Historyczny' },
    27: { name: 'Horror' },
    10402: { name: 'Muzyczny' },
    9648: { name: 'Tajemnica' },
    10749: { name: 'Romans' },
    878: { name: 'Sci-Fi' },
    10770: { name: 'film TV' },
    53: { name: 'Thriller' },
    10752: { name: 'Wojenny' },
    37: { name: 'Western' },
  });
  const queries = () => {
    const url = props.match.params.id;
    const url_to_match = url;
    const regex = /(\d+)/;
    const movie_id = url_to_match.match(regex) && url_to_match.match(regex)[0];
    setMovieId(movie_id);
    fetch(
      `https://api.themoviedb.org/3/movie/${movie_id}?api_key=${api_key}&append_to_response=external_ids,images,recommendations,keywords,credits&include_image_language=en,pl,null&language=pl-PL`
    )
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'movie';
          setItem(result);
          setIsLoaded(true);
          if (movie_id && result.title) {
            if (
              encodeURI(props.location.pathname) !==
              encodeURI(`/movie/${movie_id}-${result.title.toLowerCase().replace(/\//g, '').split(' ').join('-')}`)
            ) {
              props.history.replace(encodeURI(`/movie/${movie_id}-${result.title.toLowerCase().replace(/\//g, '').split(' ').join('-')}`));
            }
          }
          if (result.title && result.release_date) {
            document.title = `Film: ${result.title} (${result.release_date.slice(0, 4)})`;
          } else {
            document.title = 'InfCinema';
          }
        },
        (error) => {
          setIsLoaded(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/${movie_id}/videos?api_key=${api_key}&language=en-Us`)
      .then((res) => res.json())
      .then(
        (result) => {
          setVideos(result);
        },
        (error) => {
          setIsLoaded2(false);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  };
  let location = useLocation();
  useEffect(() => {
    setError(null);
    setIsLoaded(false);
    setIsLoaded2(false);
    setItem('');
    setVideos('');
    setActorsCount(14);
    setVid(2);
    setArt1(6);
    setArt2(6);
    setGalery(false);
    setModalImg(false);
    setShowImages(false);
    setMovieId('');
    queries();
  }, [location]);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <GoBack>
          <ArrowBackIcon
            fontSize='large'
            onClick={() => {
              if (props.history.length > 2) {
                props.history.goBack();
                // setBug(Math.random());
              } else props.history.replace('');
            }}
          />
        </GoBack>
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwrz w penej rozdzielczoci
          </OpenFull>
        </PhotoGallery>
        <BackgroundCont container spacing={0} url={`https://image.tmdb.org/t/p/w1280${item.backdrop_path && item.backdrop_path}`}>
          <DetailWrapper container>
            <LeftSide item xs={12} md={4} lg={4}>
              <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
                {havePhoto(item, 'big')}
                {item.external_ids && (
                  <SocialHr>
                    {item.homepage && (
                      <a href={item.homepage} target='_blank' rel='noopener noreferrer'>
                        <HomeIcon style={{ color: '#333' }} />
                      </a>
                    )}
                    {item.external_ids.instagram_id && (
                      <a href={`https://www.instagram.com/${item.external_ids.instagram_id}`} target='_blank' rel='noopener noreferrer'>
                        <InstagramIcon style={{ color: '#E1306C' }} />
                      </a>
                    )}
                    {item.external_ids.facebook_id && (
                      <a href={`https://www.facebook.com/${item.external_ids.facebook_id}`} target='_blank' rel='noopener noreferrer'>
                        <FacebookIcon style={{ color: '#4267B2' }} />
                      </a>
                    )}
                    {item.external_ids.twitter_id && (
                      <a href={`https://www.twitter.com/${item.external_ids.twitter_id}`} target='_blank' rel='noopener noreferrer'>
                        <TwitterIcon style={{ color: '#1da1f2' }} />
                      </a>
                    )}
                    {item.id && (
                      <a href={`https://www.themoviedb.org/movie/${item.id}`} target='_blank' rel='noopener noreferrer'>
                        <TmdbIcon alt='tmdb.com' src={ImdbIcon} />
                      </a>
                    )}
                    {item.external_ids.imdb_id && (
                      <a href={`https://www.imdb.com/title/${item.external_ids.imdb_id}`} target='_blank' rel='noopener noreferrer'>
                        <img alt='imdb.com' src={ImdbIcon} />
                      </a>
                    )}
                    {item.external_ids.imdb_id ? (
                      <a href={`https://www.trakt.tv/search/imdb?query=${item.external_ids.imdb_id}`} target='_blank' rel='noopener noreferrer'>
                        <TraktIcon />
                      </a>
                    ) : (
                      <a href={`https://www.trakt.tv/search/tmdb?query=${item.id}`} target='_blank' rel='noopener noreferrer'>
                        <TraktIcon />
                      </a>
                    )}
                    {item.external_ids.tvdb_id && (
                      <a href={`https://www.thetvdb.com/dereferrer/series/${item.external_ids.tvdb_id}`} target='_blank' rel='noopener noreferrer'>
                        <img alt='tvdb.com' src={TvdbIcon} />
                      </a>
                    )}
                    {item.id && (
                      <a href={`https://fanart.tv/movie/${item.id}`} target='_blank' rel='noopener noreferrer'>
                        <img alt='fanart.tv' src={FanartIcon} />
                      </a>
                    )}
                  </SocialHr>
                )}{' '}
              </div>
            </LeftSide>

            <RightSide item xs={12} md={8} lg={8} xl={8}>
              <ITitle>
                <div className='iTitle'>
                  {item.title}
                  <span>{item.release_date && item.release_date.slice(0, 4)}</span>
                </div>
                {item.original_title !== item.title && (
                  <div className='originalTitle'>{item.original_title ? item.original_title : 'Brak danych'}</div>
                )}
              </ITitle>
              <BasInfo>
                <div className='genres'>{item.genres?.length > 0 ? item.genres.map((i) => i.name).join(', ') : 'Brak danych'}</div>
                <div className='runtime'>{item.runtime > 0 ? `${item.runtime} minut` : 'Brak danych'}</div>
                <div className='date'>{item.release_date ? item.release_date : 'Brak danych'}</div>
                <div className='rating'>
                  <StarIcon style={{ color: 'orange', fontSize: '30px' }} />
                  {item.vote_average * 10}%
                </div>
              </BasInfo>
              <Overview>{item.overview ? item.overview : 'Brak polskiego opisu'}</Overview>
              <Grid container>
                <Grid item xs={12} md={9}>
                  <Credits>
                    <div>
                      Reyseria:{' '}
                      <span>
                        {item.credits?.crew.filter((item) => item.job === 'Director').length > 0
                          ? item.credits?.crew
                              .filter((item) => item.job === 'Director')
                              .map((i, index) => (
                                <span key={index}>
                                  <Link to={encodeURI(`/person/${i.id}-${i.name.toLowerCase().split(' ').join('-')}`)}>{i.name}</Link>
                                  {index === item.credits?.crew.filter((item) => item.job === 'Director').length - 1 ? '' : ' , '}
                                </span>
                              ))
                          : 'Brak danych'}
                      </span>
                    </div>
                    <div>
                      Scenariusz:{' '}
                      <span>
                        {item.credits.crew.filter((item) => item.job === 'Writer').length > 0
                          ? item.credits.crew
                              .filter((item) => item.job === 'Writer')
                              .map((i, index) => (
                                <span key={index}>
                                  <Link to={encodeURI(`/person/${i.id}-${i.name.toLowerCase().split(' ').join('-')}`)}>{i.name}</Link>
                                  {index === item.credits.crew.filter((item) => item.job === 'Writer').length - 1 ? '' : ' , '}
                                </span>
                              ))
                          : 'Brak danych'}
                      </span>
                    </div>
                  </Credits>
                  <div>
                    {videos?.results?.length > 0 && (
                      <div
                        style={{
                          display: 'flex',
                          flexDirection: 'column',
                          alignItems: 'flex-start',
                          width: '100%',
                        }}>
                        {videos?.results.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 0 && (
                          <div style={{ margin: '30px 20px 10px 30px', fontSize: '20px' }}>Zwiatun:</div>
                        )}
                        {videos?.results && (
                          <VideoWrapper>
                            {videos.results
                              .filter((item) => item.type === 'Trailer' && item.site === 'YouTube')
                              .slice(0, vid)
                              .map((video, index) => (
                                <ResponsiveVideoOuter min={(vid > 2).toString()} key={index}>
                                  <ResponsiveVideoWrapper>
                                    <iframe
                                      style={{ position: 'absolute', top: '0', left: '0', right: '0', bottom: '0', width: '100%', height: '100%' }}
                                      allowFullScreen='allowfullscreen'
                                      mozallowfullscreen='mozallowfullscreen'
                                      msallowfullscreen='msallowfullscreen'
                                      oallowfullscreen='oallowfullscreen'
                                      webkitallowfullscreen='webkitallowfullscreen'
                                      title={video.name}
                                      src={`https://www.youtube.com/embed/${video.key}`}></iframe>
                                  </ResponsiveVideoWrapper>
                                </ResponsiveVideoOuter>
                              ))}
                          </VideoWrapper>
                        )}
                      </div>
                    )}
                    {videos?.results?.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 2 && (
                      <Grid item xs={12} onClick={() => handleMoreVideos()}>
                        {vid > 6 ? <SeeMore2>Poka mniej</SeeMore2> : <SeeMore2>Poka wicej</SeeMore2>}
                      </Grid>
                    )}
                  </div>
                </Grid>
                <Grid item xs={12} md={3}>
                  <CompanyTitle>Wytwrnie</CompanyTitle>
                  <CompanyWrapper>
                    {item.production_companies
                      ? item.production_companies.map((company, index) => {
                          if (company.logo_path) {
                            return (
                              <CompanyImage key={index}>
                                <img src={`https://image.tmdb.org/t/p/w92${company.logo_path}`} />
                              </CompanyImage>
                            );
                          }
                        })
                      : 'Brak Danych'}
                  </CompanyWrapper>
                </Grid>
              </Grid>
            </RightSide>
          </DetailWrapper>
        </BackgroundCont>
        <Sec>Obsada:</Sec>
        <ItemWrapper>
          {item.credits?.cast?.slice(0, actorsCount).map((person, index) => {
            return (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item to={encodeURI(`/person/${person.id}-${person.name?.toLowerCase().split(' ').join('-')}`)}>
                  {havePhoto(person)}
                  <div className='tit2'>
                    {person.name}
                    <br />
                    <span className='character'>{person.character}</span>
                  </div>
                </Item>
              </Grow>
            );
          })}
        </ItemWrapper>
        {item.credits?.cast?.length > 14 && (
          <div onClick={handleMore}>{actorsCount > 14 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}</div>
        )}
        {item.recommendations?.results?.length > 0 && <Sec>Rekomendacje:</Sec>}
        <ItemWrapper>
          {item.recommendations?.results?.slice(0, 14).map((movie, index) => {
            movie.media_type = 'movie';
            return (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item to={encodeURI(`/movie/${movie.id}-${movie.title?.toLowerCase().split(' ').join('-')}`)}>
                  {havePhoto(movie)}|
                  <div className='rating'>
                    <StarIcon style={{ color: 'orange', fontSize: '12px' }} />
                    {movie.vote_average * 10}%
                  </div>
                  <div className='tit2'>
                    <div className='infoaboutmovie'>
                      {movie.title} {movie.release_date && `(${movie.release_date.slice(0, 4)})`}
                      <br />
                      <span className='character'>
                        {movie.genre_ids?.length > 0 && movie.genre_ids.map((i) => (getGenres()[i] ? getGenres()[i].name : null)).join(', ')}
                      </span>
                    </div>
                  </div>
                </Item>
              </Grow>
            );
          })}
        </ItemWrapper>
        {item.images.status !== 'error' && <Sec style={{ marginBottom: '30px' }}>Grafiki:</Sec>}
        {showImages ? (
          <>
            {item.images.backdrops && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Ta
                </CenterTitle>
                {item.images.backdrops.slice(0, art1).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {item.images.backdrops.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(1)}>
                    {art1 > 6 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}
                  </Grid>
                )}{' '}
              </OutWrp>
            )}
            {item.images.posters && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Plakaty
                </CenterTitle>
                {item.images.posters.slice(0, art2).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {item.images.posters.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(2)}>
                    {art2 > 6 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}
                  </Grid>
                )}
              </OutWrp>
            )}
          </>
        ) : (
          <ImagesTrigger onClick={() => setShowImages(true)}>
            Zaaduj Grafiki <ExpandMoreIcon fontSize='large' />
          </ImagesTrigger>
        )}
      </>
    );
  }
}

export default Detailsmovie;
