import styled from 'styled-components';
import Grid from '@material-ui/core/Grid';

export const DetailWrapper = styled(Grid)`
  width: 1440px;
  min-height: 100vh;
  margin-right: auto;
  margin-left: auto;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  @media (min-width: 960px) {
    padding-left: 50px;
  }
  padding-top: 50px;
  padding-bottom: 50px;
`;
export const LeftSide = styled(Grid)`
  display: flex;
  justify-content: center;
  @media (min-width: 960px) {
    justify-content: flex-end;
    padding: 0;
  }
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;

  /* padding-left: 40px;
  padding-right: 40px; */
`;
export const RightSide = styled(Grid)`
  width: 100%;
  font-weight: bold;
  color: white;
`;
export const ITitle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 25px;
  @media (min-width: 1200px) {
    .iTitle {
      font-size: 30px;
      span {
        font-size: 24px;
      }
    }
    .originalTitle {
      font-size: 15px;
    }
  }
  .iTitle {
    font-size: 26px;
    span {
      color: gray;
      font-size: 20px;
      margin-left: 5px;
    }
  }
  .originalTitle {
    font-size: 16px;
    color: #457c85;
  }
`;
export const BasInfo = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.2);
  @media (min-width: 1200px) {
    font-size: 19px;
    padding: 20px 35px;
  }
  @media (min-width: 1440px) {
    font-size: 20px;
    padding: 20px 35px;
  }
  font-size: 14px;
  padding: 10px 25px;
  .genres {
  }
  .runtime {
    color: #82ff77;
  }
  .date {
    color: #74cdff;
  }
  .rating {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;
export const Overview = styled.div`
  font-size: 15px;
  @media (min-width: 1200px) {
    font-size: 22px;
  }
  @media (min-width: 960px) {
    font-size: 18px;
  }
  font-weight: lighter;
  padding: 30px;
  color: #eee;
  background-color: rgba(0, 0, 0, 0.5);
`;
export const CompanyImage = styled.div`
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  height: 80px;
  width: 80px;
  padding: 10px;
  img {
    max-height: 60px;
    max-width: 100%;
  }
`;
export const CompanyWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-right: 30px;
`;
export const CompanyTitle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 10px 0;
`;
export const Credits = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 30px;
  color: gray;
  font-size: 16px;
  border-top: 1px solid white;
  padding-top: 10px;
  div {
    margin: 8px;
  }
  span {
    color: white;
  }
  a {
    color: white;
  }
`;
